.shoppingcart-container {
    padding: 15rem 4rem;
    background-image: linear-gradient(  rgb(0, 0, 0) , rgb(197, 0, 0) );
    width: 100%;
    /* height: 100vh; */
  }
  .shoppingcart-container h2 {
    font-weight: 400;
    font-size: 30px;
    text-align: center;
    color: white;
  }
  .shoppingcart-container .shopping-titles {
    margin: 2rem 0 1rem 0;
    color: white;
  }
  .shoppingcart-container .shopping-titles h3 {
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    color: white;
  }
  .shoppingcart-item,
  .shoppingcart-container .shopping-titles {
    display: grid;
    align-items: center;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    column-gap: 0.5rem;
    color: white;
  }
  .shoppingcart-item {
    border-top: 1px solid rgb(187, 187, 187);
    padding: 1rem 0;
  }
  .shoppingcart-container .shopping-titles .product-title {
    padding-left: 0.5rem;
    color: white;
  }
  .shoppingcart-container .shopping-titles .total {
    padding-right: 0.5rem;
    justify-self: right;
    color: white;
  }
  .shoppingcart-item .shoppingcart-product {
    display: flex;
  }
  .shoppingcart-item .shoppingcart-product img {
    width: 100px;
    max-width: 100%;
    margin-right: 1rem;
  }
  .shoppingcart-item .shoppingcart-product h3 {
    font-weight: 400;
    color: white;
  }
  .shoppingcart-item .shoppingcart-product button {
    border: none;
    outline: none;
    margin-top: 0.7rem;
    cursor: pointer;
    background: none;
    color: gray;
  }
  .shoppingcart-item .shoppingcart-product button:hover {
    color: white;
  }
  
  .shoppingcart-item .shoppingcart-product-quantity {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 130px;
    max-width: 100%;
    border: 0.5px solid rgb(177, 177, 177);
    border-radius: 5px;
    color: white;
  }
  .shoppingcart-item .shoppingcart-product-quantity button {
    border: none;
    outline: none;
    background: none;
    padding: 0.7rem 1.5rem;
    cursor: pointer;
    color: white;
  }
  .shoppingcart-item .shoppingcart-product-quantity .count {
    padding: 0.7rem 0;
    color: white;
  }
  .shoppingcart-item .shoppingcart-product-total-price {
    padding-right: 0.5rem;
    justify-self: right;
    font-weight: 700;
    color: white;
  }
  
  /* shoppingcart summary */
  .shoppingcart-summary {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-top: 1px solid rgb(187, 187, 187);
    padding-top: 2rem;
    color: white;
  }
  .shoppingcart-summary .clear-shoppingcart {
    width: 130px;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    border: 0.5px solid rgb(177, 177, 177);
    color: white;
    background: none;
    outline: none;
    cursor: pointer;
  }
  .shoppingcart-checkout {
    width: 270px;
    max-width: 100%;
  }
  .shoppingcart-checkout .subtotal {
    display: flex;
    justify-content: space-between;
    font-size: 20px;
  }
  .shoppingcart-checkout .amount {
    font-weight: 700;
  }
  .shoppingcart-checkout p {
    font-size: 14px;
    font-weight: 200;
    margin: 0.5rem 0;
  }
  .shoppingcart-checkout button {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    font-weight: 400;
    letter-spacing: 1.15px;
    background-color: #4b70e2;
    color: #f9f9f9;
    border: none;
    outline: none;
    cursor: pointer;
  }
  .continue-shopping,
  .start-shopping {
    margin-top: 1rem;
  }
  .continue-shopping a,
  .start-shopping a {
    color: white;
    text-decoration: none;
    display: flex;
    align-items: center;
  }
  .continue-shopping a span,
  .start-shopping a span {
    margin-left: 0.5rem;
  }
  .shoppingcart-empty {
    font-size: 20px;
    margin-top: 2rem;
    color: rgb(84, 84, 84);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* Responsive */
  
  @media (max-width: 665px) {
    /* NavBar */
    .nav-bar {
      padding: 0 1rem;
    }
    .nav-bar h2 {
      font-size: 30px;
    }
  
    /* shoppingcart */
    .shoppingcart-container {
      padding: 2rem;
    }
    .shoppingcart-container .shopping-titles {
      display: none;
    }
    .shoppingcart-item,
    .shoppingcart-container .shopping-titles {
      grid-template-columns: 1fr;
      row-gap: 1rem;
    }
    .shoppingcart-item .shoppingcart-product-total-price {
      justify-self: left;
    }
    .shoppingcart-summary {
      flex-direction: column;
    }
    .shoppingcart-summary .clear-btn {
      margin-bottom: 2rem;
    }
  }